.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.d-flex{
  display: flex;
  align-items: center;
}
.space-between{
  justify-content: space-between;
}
.fileContainer{
  box-shadow: none !important;
  padding: 0px 0 !important;
}
.fileContainer .uploadPictureContainer{
  margin:10px !important;
}
.login {
  width: 100%;
  height: 100%;
}
.marginTop{
  margin-top: 1rem;
}
.marginBottom{
  margin-bottom: 1rem;
}
.paddingTop{
  padding-top: 1rem;
}
.paddingBottom{
  padding-bottom: 1rem;
}
.property-section{
  display: flex;
  justify-content: space-between;
}
.property-section li{
  width: 100%;
}
.property-section a{
  background: #f7f7f7;
  border: #e2e2e2 1px solid;
  color: #212121;
  padding: 1rem;
  border-radius: 5px;
  margin-right: 1rem;
  font-weight: bold;
  display: block;
  text-decoration: none;
}
.property-section a:hover{
  background: #e2e2e2;
}
.property-section a.active1{
  background-color: #26a69a;
  color: #f7f7f7;
}
.sidebar .category{font-size: 20px}

.custom-file-upload {
  border-radius: 50%;
  display: inline-block;
  position: relative;
  padding: 0px;
  cursor: pointer;
  background: linear-gradient(270deg, #3fa1a9, #79f1a4);
  margin-bottom: 25px;
}
.custom-file-upload img {
  width: auto;
  height: 100%;
}
.img-wrap{
  position: relative;
  width: 220px;
  height: 220px;
  overflow: hidden;
  border-radius: 50%;
  display: inline-block;
    font: normal normal normal 14px/1 FontAwesome;
    font-size: inherit;
    text-rendering: auto;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-transform: translate(0, 0);
    -ms-transform: translate(0, 0);
    transform: translate(0, 0);
}
.img-upload:before{
  content: "\f093";
  font-size: 90px;
  position: absolute;
  padding-top: 46px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #63d3a6;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  opacity: 0;
  transition: .5s ease;
  background-color: #fff;
}
.img-upload:hover:before{
 opacity: 1;
}
.custom-file-upload input[type="file"] {
  display: none;
}